.navbarContainer {
	padding-left: 40px;
  	padding-right: 40px; 
	background-color: black;
	color: white;
	display: flex;
	justify-content: space-between;
	align-items: center;
	position: sticky;
	height: 100px;
	top:0;

	
} 

.navbarNavigationLinks {
	display: none;
	justify-content: space-between;
	align-items: center;
	gap:50px;
	
}

.hamburgerMenu {
	background-color: black;
	color: white;
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	cursor: pointer;
	height: 0px;
	transition: height 0.4s ease;
	overflow: hidden;
	gap:20px;

}

.menuChange .hamburgerMenu  {
	height: 250px;
	transition:  height 0.4s ease;
}

.navbarIntials {
	flex:1;
}

.hamburgerButton {

	margin: 0;
  	padding: 0;
}

.bar1, .bar2, .bar3 {
  width: 35px;
  height: 5px;
  background-color: white;
  margin: 6px 0;
  transition: all 0.25s linear;
}

.change .bar1 {
  transform: translate(0, 11px) rotate(-45deg);
  transition: all 0.25s linear;
}

.change .bar2 {opacity: 0;
  transition: all 0.25s linear;
  background-color: black;
}

.change .bar3 {
  transform: translate(0, -11px) rotate(45deg);
  transition: all 0.25s linear;
}

@media screen and (min-width: 769px) {
    /* large tablets & small laptops */
    /* .links {
        display: flex;
        justify-content: flex-end;
    } */
	 .navbarSocialLinks {
		display:flex;
	}
	.navbarNavigationLinks {
		display:flex;
	}
    .hamburgerButton {
        display: none;
    }
	.hamburgerMenu {
		display: none;
	}
    
    
}