.about {
	display: flex;
	flex-direction: column;
	gap: 3rem;
	font-size: 1.5rem;
	align-items: flex-start;
}

.header {
	gap: 1rem;
	display: flex;
	flex-direction: column;
	width: 100%;
	align-items: center;
}

.message {
	font-size: 3rem;
}

.image {
	height: 200px;
	border-radius: 50%;
	object-fit: cover;
	aspect-ratio: 1 / 1;
	align-self: flex-start;
}

.links {
	gap: 1rem;
	display: flex;
}

.link {
	color: black;
	transition: color 0.3s ease;
	text-decoration: underline;
	cursor: pointer;
}

.button {
	padding: 15px 30px;
	font-size: 1.5rem;
	background-color: black;
	color: white;
	border: none;
	border-radius: 5px;
	cursor: pointer;
	transition: background-color 0.3s ease;
	display: inline-block;
}

@media screen and (min-width: 481px) {
	.header {
		flex-direction: row;
		justify-content: space-between;
	}
}

@media screen and (min-width: 769px) {
	.image {
		width: 300px;
		height: 300px;
	}
	.message {
		font-size: 4rem;
	}
	.name {
		display: block;
	}
}

@media screen and (min-width: 1280px) {
	.message {
		font-size: 5rem;
	}
}

